:global .debug.aria [aria-label]::after,
:global .debug.aria [aria-valuetext]::after {
  position: absolute;
  font-size: small;
  font-weight: 100;
  background-color: #ffaa0099;
  color: white;
  border: 1px solid #ffaa00;
  border-radius: 0.5em;
  border-top-left-radius: 0;
  bottom: 0;
  padding: 0 1ex;
  transform: translateY(100%);
  line-height: 1.3em;
  z-index: 2;
  pointer-events: none;
}
:global .debug.aria [aria-label]::after {
  content: "Aria Label:\0020" attr(aria-label);
}
