/*
 * Provide the height of the sprite and an index value of how far to vertically offset this sprite
 * 0 for the first position (top), 1 below that, 2 below 1, etc.
 * This way, selected/focused states can refer to other sprite indexes easily.
 * 0 for normal/resting
 * 1 for focused
 */
.icon {
  --agate-icon-sprite-count: 1;
}
.icon:global(.carbon) {
  width: 2rem;
  height: 2rem;
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 2rem * -0);
  background-size: 2rem calc(var(--agate-icon-sprite-count, 1) * 2rem);
  background-repeat: no-repeat;
  margin: 0.5rem;
  font-family: "Agate Icons";
  font-size: 4rem;
  line-height: 2rem;
  text-align: center;
  position: relative;
  color: inherit;
  letter-spacing: inherit;
  text-indent: initial;
}
.icon:global(.carbon).huge {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 7.91667rem * -0);
  background-size: 7.91667rem calc(var(--agate-icon-sprite-count, 1) * 7.91667rem);
  width: 7.91667rem;
  height: 7.91667rem;
  font-size: 13.33333rem;
  line-height: 7.91667rem;
}
.icon:global(.carbon).small {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 1.5rem * -0);
  background-size: 1.5rem calc(var(--agate-icon-sprite-count, 1) * 1.5rem);
  width: 1.5rem;
  height: 1.5rem;
  font-size: 3rem;
  line-height: 1.5rem;
}
.icon:global(.carbon).smallest {
